import axios from "axios";
import { Container, Button, Card } from "react-bootstrap";
import { connect } from "react-redux";

function bagCard(itemProps) {
  // this gets called every single time a bag is made
  const loopOrder = () => {
    const list = [];
    itemProps.item.forEach((i) => {
      let addMe = <Card.Text className = "display-20 text-center">{i}</Card.Text>
      list.push(addMe);
    })
    return list;
  }

  const removeBagItem = (item) => {
    axios.post("/users/removeUserItem", {
      params: {
        bagItem: item
      }

    }).then((res) => {
      console.log(res.data);
      console.log("removed item")

    });
  }
  const getPrice = () => {
    let totalPrice = 0;
    itemProps.cost.forEach((p) => {
      if(p != ""){
        totalPrice += parseFloat(p);
      }
    })
    return totalPrice;
  }

  return (
    <Card style={{ width: '18rem' }}>
        <Card.Body>
          <Card.Title  className = "display-10 text-center">{itemProps.name}</Card.Title>
          <Card.Subtitle>${getPrice()}</Card.Subtitle>

          {loopOrder()}

          {/* add a button to delete items */}
          <Button variant="primary" type="submit" onClick={() => removeBagItem(itemProps.item)}> - </Button>

          {/* add a button to edit bowl/plate/bigger plate */}
          <Button variant="secondary" type="submit"> Edit </Button>
        </Card.Body>
    </Card>
  )
}

export default bagCard;