import { Container, Button, Nav, CardGroup, Card, Spinner, Form, Table, Row, Col, Modal } from "react-bootstrap";
import { useSpring, animated } from "react-spring";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import React, { useState, useEffect } from "react";
import axios from "axios";
import BagItemCard from "../Cards/bagCard";
import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import ReportItemCard from "../Cards/reportItemCard";

function Reports(props){
    const [isLoading, setLoading] = useState([]);
    const [report, setReport] = useState([]);
    const [show, setShow] = useState(false);
    const [startDate, setStartDate] = useState("2022-09-26");
    const [endDate, setEndDate] = useState("2022-09-27");
    const [revenue, setRevenue] = useState(0);
    const [reportToGenerate, setReportToGenerate] = useState("");
    const [extendCommand, addCommand] = useState("");
    const [reportItems, setReportItems] = useState([]);

    const formatter = new Intl.NumberFormat("en-US", {
        style: "currency",
        currency: "USD"
      });

    const getRevenueReport = () => {
        axios.get("/reports/getRevenue", {
            params: {
                endDate: endDate,
                startDate: startDate,
                extendCommand: extendCommand
            }
        }).then((res) => {
            console.log(res.data);
            let tempCard = <ReportItemCard  
                            key={"Revenue"}
                            name={"Total Revenue"}
                            detail={formatter.format(res.data.revenue)}
                            />;
            setReportItems([tempCard]);
        });
        console.log(revenue);
    };

    const getExcessReport = () => {
        axios.get("/reports/getExcessReport", {
            params: {
                startDate: startDate,
                endDate: endDate
            }
        })
        .then((res) => {
            let excessItems = res.data;
            let tempReportItems = [];
            excessItems.forEach((item) => {
               let tempCard = <ReportItemCard  
                                key={item.item}
                                name={item.item}
                                detail={(item.percentageSold * 100) + "% of stock sold"}
                                />;
                tempReportItems.push(tempCard);
            });
            setReportItems(tempReportItems);
        })
        .catch((err) => {console.log(err);});
    }
    
    useEffect(() => {
        setLoading(false);
    }, [props.auth.user.id]);


    const generateReport = () => {
        if(reportToGenerate == "Total Revenue"){
            getRevenueReport();
        }
        else if(reportToGenerate == "Revenue By ID"){
        }
        else if(reportToGenerate == "Excess"){
            getExcessReport();
        }
        else if(reportToGenerate == "Sell Together"){

        }
        else{
            console.log("No report selected");
            return;
        }
    }
  
      return (
  
        <div className="Reports">
          {isLoading ? (
            <div className="d-flex justify-content-center">
              <Spinner animation="border" />
            </div>
          ) : (
            <React.Fragment>
                <br></br>
                <h2 className="display-8 text-center">Manager Reports</h2>
                <br></br>
                <form style={{width: 50+"%", float: "left"}}>
                <div class="form-group row">
                    <label for="report" class="col-sm-2 col-form-label" align="right">Start Date</label>
                        <div class="col-sm-3">
                    <input type="date" class="form-control" id="startDate" name="startDate" min="2022-09-26" 
                        onChange={(e) => {
                        var minDate = e.target.value;
                        document.getElementById('endDate').setAttribute("min", minDate);
                        setStartDate(minDate);
                      }}></input>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="report" class="col-sm-2 col-form-label" align="right">End Date</label>
                    <div class="col-sm-3">
                        <input type="date" class="form-control" id="endDate" name="endDate" min="2022-09-26" 
                        onChange={(e) => {
                        var maxDate = e.target.value;
                        setEndDate(maxDate);
                      }}></input>
                    </div>
                </div>
                <fieldset class="form-group">
                    <div class="row">
                    <legend class="col-form-label col-sm-2 pt-0" align="right">Choose Report</legend>
                    <div class="col-sm-3">
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name="gridReport" id="report" value="option1" onClick={() => setReportToGenerate("Total Revenue")}></input>
                            <label class="form-check-label" for="gridRadios1">Total Revenue</label>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name="gridReport" id="report" value="option2" onClick={() => setReportToGenerate("Revenue By ID")}></input>
                            <label class="form-check-label" for="gridRadios1">Revenue by ID</label>
                        </div>
                        <div class="form-check">
                            <input class="form-check-input" type="radio" name="gridReport" id="report" value="option3" onClick={() => setReportToGenerate("Excess")}></input>
                            <label class="form-check-label" for="gridRadios2">Excess</label>
                        </div>
                        <div class="form-check disabled">
                            <input class="form-check-input" type="radio" name="gridReport" id="report" value="option4" onClick={() => setReportToGenerate("Sell Together")}></input>
                            <label class="form-check-label" for="gridRadios3">Sell-Together</label>
                        </div>
                    </div>
                    </div>
                </fieldset>
                <div className="align-items-center">
                    <div class="col-sm-10" style={{textAlign: "center"}}>
                        <Button class="btn btn-primary" align="right" onClick={() => generateReport()}>Process</Button>
                    </div>
                </div>
            </form>
            <div className="Report Generated" style={{width: 48+"%", float: "right"}}>
                {reportItems}
            </div>
            </React.Fragment>
        )
      }
      </div>
      );
  
  
  }
  
Reports.propTypes = {
    auth: PropTypes.object.isRequired,
  };
  
  const mapStateToProps = (state) => ({
    auth: state.auth,
  });
  
  export default connect(mapStateToProps)(Reports);