import { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { Container, Spinner, Table, Card, Button, CardGroup, Modal } from "react-bootstrap";
import bowl from "./images/bowlnobg.png";
import plate from "./images/pandaPlate.png";
import eggroll from "./images/eggrollsnobg.png";
import drinkImg from './images/drinksnobg.png';
import bigger from './images/biggerplate.png';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import UserCard from '../Cards/userCard';
import MenuItem from './menuItem';


import axios from "axios";
import React from "react";

const userCardStyle = {
    width: "18rem",
    cursor: "pointer"
}

function Menu(props) {
    const [tempEntrees, setTempEntrees] = useState([]);
    const [tempSize, setTempSize] = useState("");
    const [tempSides, setTempSides] = useState([]);
    const [tempDrinks, setTempDrinks] = useState([]);
    const [tempApps, setTempApps] = useState([]);
    
    const [show, setShow] = useState(false);

    const [isLoading, setLoading] = useState(true);
    const [sides, setSides] = useState([]);
    const [entrees, setEntrees] = useState([]);
    const [title, setTitle] = useState("N/A");
    const [drinks, setDrinks] = useState([]);
    const [appetizers, setAppetizers] = useState([]);
    
    const [numAllowed, setNumAllowed] = useState(0);

    // new version
    const [entreecount, setEntreeCount] = useState(0);
    const [drinkcount, setDrinkCount] = useState(0);
    const [appcount, setAppCount] = useState(0);
    const [sidecount, setSideCount] = useState(0);
    const [entreePrice, setEntreePrice] = useState([]);
    const [drinkPrice, setDrinkPrice] = useState([]);
    const [sidePrice, setSidePrice] = useState([]);
    const [appPrice, setAppPrice] = useState([]);
    const [sizePrice, setSizePrice] = useState("");
    const [loggedIn, setLogin] = useState(false);

    let list = [];

    const handleClose = () => {
        setShow(false);
        setNumAllowed(0);
        setEntreeCount(0);
        setDrinkCount(0);
        setAppCount(0);
        setSideCount(0);
        setTempSides("");
        setTempEntrees([]);
        setTempSize([]);
        setTempDrinks([]);
        setTempApps([]);
        
    }

    const Submit = () => {
        let order = (tempSize === "" ? "" : tempSize) + (tempEntrees.join('; ') === "" ? "" : "; " + tempEntrees.join('; ')) + (tempSides.join('; ') === "" ? "" : "; " + tempSides.join('; ')) + (tempApps.join('; ') === "" ? "" : "; " + tempApps.join('; ')) + (tempDrinks.join('; ') === "" ? "" : "; " + tempDrinks.join('; '));
        let price = sizePrice + (entreePrice.join('; ') === "" ? "" : "; " + entreePrice.join('; ')) + (sidePrice.join('; ') === "" ? "" : "; " + sidePrice.join('; ')) + (appPrice.join('; ') === "" ? "" : "; " + appPrice.join('; ')) + (drinkPrice.join('; ') === "" ? "" : "; " + drinkPrice.join('; '));
        console.log(price);
        axios.post("/orders/SubmitToBag", {
            params: {
              order: order,
              price: price,
              employeeID: props.auth.user.id
            }
          })
          .then((res) => {
            console.log(res.data);
            setOrder([]);
            window.location.reload();
          })
        order = ""; 
        setShow(false);
    }

    const openModal = (type) => {
        
        setTitle(type);
        if (type === "Bowl") {
            setNumAllowed(1);
            setTempSize("Bowl");
            setSizePrice("7.5");
        }
        else if (type === "Plate") {

            setNumAllowed(2);
            setTempSize("Plate");
            setSizePrice("9");
        }
        else if (type === "Bigger Plate") {
            setNumAllowed(3);
            setTempSize("Bigger Plate");
            setSizePrice("10.5");
        }
        
        setShow(true);
    }

    const names = [
        {title: "Bowl", text: "1 side & 1 entree", image: bowl, link: "/mainCourse"}, 
        {title: "Plate", text: "1 side & 2 entrees", image: plate, link: "/mainCourse"}, 
        {title: "Bigger Plate", text: "1 side & 3 entrees", image: bigger, link: "/mainCourse"},
        {title: "Drinks", text: "Add a drink", image: drinkImg, link: "", type: 1},
        {title: "Appetizers", text: "Start your meal with something small", image: eggroll, link: ""},
    ]

    useEffect(() => {
        
        axios.get("/menu/getSides").then(res => {
            setSides(res.data);
        });
        axios.get("/menu/getEntrees").then(res => {
            setEntrees(res.data);
        });
        axios.get("/menu/getDrinks").then(res => {
            setDrinks(res.data);
        });
        axios.get("/menu/getAppetizer").then(res => {
            setAppetizers(res.data);
        })
        if (props.auth.user.id > 0) {
            setLogin(true);
        }
        else {
            setLogin(false);
        }
        setLoading(false);
    }, [props.auth.user.id]);

    return (
        <div className="Menu">
            <Container>
                {
                    isLoading
                        ? (
                            <div className="d-flex justify-content-center">
                                <Spinner animation="border" />
                            </div>
                        )
                        : (
                            <React.Fragment>
                                <h2 className="display-4 text-center">
                                    Menu
                                </h2>
                                <Modal show={show} onHide={handleClose}>
                                    <Modal.Header closeButton>
                                    <Modal.Title>{title}</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <Form>
                                            
                                            {numAllowed == 0 ? (
                                                null
                                            ) : (
                                                <React.Fragment>
                                                    <Form.Label>Choose a side</Form.Label>
                                                    {sides.map((obj, idx) => (
                                                        <MenuItem key={idx} item={obj.item} max={2} current={sidecount} function={setSideCount} setbag={setTempSides} bag={tempSides}  setprice={setSidePrice} price={sidePrice} itemPrice={obj.cost}/>
                                                    ))}
                                                    <Form.Label>Choose {numAllowed} entree(s)</Form.Label>
                                                    {entrees.map((obj, idx) => (
                                                        <MenuItem key={idx} item={obj.item} max={numAllowed} current={entreecount} function={setEntreeCount} setbag={setTempEntrees} bag={tempEntrees} setprice={setEntreePrice} price={entreePrice} itemPrice={obj.cost}/>
                                                    ))}
                                                </React.Fragment>
                                            )}
                                            {title == "Drinks" ? (
                                                <>
                                                    <Form.Label>Choose a Drink</Form.Label>
                                                    {drinks.map((obj, idx) => (
                                                        <MenuItem key={idx} item={obj.item} max={999} current={drinkcount} function={setDrinkCount} setbag={setTempDrinks} bag={tempDrinks} setprice={setDrinkPrice} price={drinkPrice} itemPrice={obj.cost}/>
                                                    ))}
                                                </>
                                            ) : (null)}
                                            {title == "Appetizers" ? (
                                                <>
                                                    <Form.Label>Choose an Appetizer</Form.Label>
                                                    {appetizers.map((obj, idx) => (
                                                        <MenuItem key={idx} item={obj.item} max={999} current={appcount} function={setAppCount} setbag={setTempApps} bag={tempApps} setprice={setAppPrice} price={appPrice} itemPrice={obj.cost}/>
                                                    ))}
                                                </>
                                            ) : (null)}
                                            
                                                                                       
                                        </Form>
                                    </Modal.Body>
                                    <Modal.Footer>
                                    <Button variant="secondary" onClick={handleClose}>
                                        Cancel
                                    </Button>
                                    <Button disabled={!loggedIn} type="submit" variant="primary" onClick={Submit}>
                                        Submit To Order
                                    </Button>
                                    </Modal.Footer>
                                </Modal>
                                <Row xs={1} md={3} className="g-4">
                                    {names.map((obj, idx) => (
                                        <Col>
                                            <UserCard key={idx} onClick={openModal} type={obj.title} title={obj.title} text={obj.text} image={obj.image} />
                                        </Col>
                                    ))}
                                </Row>
                                <br></br><br></br><br></br><br></br><br></br><br></br><br></br><br></br><br></br>
                            </React.Fragment>
                        )
                }
            </Container>
        </div>
    );
}

Menu.propTypes = {
    auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
    auth: state.auth,
});

export default connect(mapStateToProps)(Menu);
